@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@200;600;900&display=swap');

* {
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Barlow', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1C1C1C;
  color: white;
}

main {
  height: 100vh;
  width: 100vw;
}

.title {
  font-size: 4rem;
  font-weight: 900;
  color: white;
  text-align: center;
  margin: 50px 0px;
}

.tip-area {
  background-color: #353535;
  padding: 15px;
  border-radius: 20px;
  width: 80vw;
  margin: auto;
  margin-bottom: 50px;
  border: solid 3px #676767;
  text-align: justify;
}

.tip-area p {
  font-size: 1.6rem;
  font-weight: 200;
}

form {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

input.text {
  height: 30px;
  width: 80vw;
  margin: auto;
  background-color: transparent;
  border-radius: 15px;
  padding: 10px;
  outline: none;
  color: white;
  font-size: 1.6rem;
  margin-bottom: 15px;
  border: solid 3px #743ad5;
}

input.button {
  height: 50px;
  width: calc(80vw + 25px);
  margin: auto;
  background-color: #743ad5;
  border-radius: 15px;
  padding: 10px;
  outline: none;
  color: white;
  font-size: 1.6rem;
  margin-bottom: 15px;
  border: solid 3px #743ad5;
  text-transform: uppercase;
  font-weight: 600;
}

.container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container p {
  font-size: 1.6rem;
  font-weight: 600;
  text-align: center;
}

img {
  margin-top: 20px;
  animation: heart-beat 1s infinite;
  animation-direction: alternate;
}

.react-toast-notifications__toast__content {
  font-size: 1.6rem !important;
}

@keyframes heart-beat {
  from {
    transform: scale(0.8);
  }

  to {
    transform: scale(1);
  }
}
